@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue";
  font-display: swap;
  src: url("../fonts/neue-montreal-regular.woff2") format("woff2"), url("../fonts/neue-montreal-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue";
  font-display: swap;
  src: url("../fonts/neue-montreal-medium.woff2") format("woff2"), url("../fonts/neue-montreal-medium.woff") format("woff");
}
html {
  background-color: #1f1f20;
}

body {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  font-family: "Neue", "Arial", sans-serif;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 20px;
}
.wrapper__border {
  display: grid;
  grid-template-columns: 0.3fr 1fr;
  grid-column-gap: 20px;
  border-radius: 25px;
  background-color: #f9f9fc;
}

.container {
  padding: 66px 20px 20px 0;
}

.title {
  display: block;
  padding: 0;
  font-weight: 500;
  font-family: "Neue", "Arial", sans-serif;
  font-size: 24px;
  line-height: 29px;
  color: #1f1f20;
}

.filter {
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  font-family: "Neue", "Arial", sans-serif;
  color: #454eec;
}

.nav-title {
  font-family: "Neue", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #64696c;
}
.nav-title:hover {
  color: #454eec;
}

.block {
  background-color: #ffffff;
  border-radius: 16px;
  padding: 20px;
}
.block__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.block__name {
  margin: 0;
}

.custom-select {
  position: relative;
  width: auto;
  padding: 9px 0 13px;
  display: flex;
  align-items: center;
}
.custom-select svg:first-of-type {
  width: 16px;
  height: 16px;
  color: #64696c;
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 5px 10px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.custom-select__button:hover {
  opacity: 0.3;
  transition: opacity 0.3s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-select__button:focus {
    opacity: 1;
  }
}
.custom-select__button:focus {
  opacity: 1;
}
.custom-select__button:disabled {
  pointer-events: none;
  opacity: 0.3;
}
.custom-select__list {
  position: absolute;
  bottom: 11px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0 0 0 9px;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item:not(:last-child) {
  margin-bottom: 13px;
}
.custom-select__item {
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  font-family: "Neue", "Arial", sans-serif;
  color: #1f1f20;
  padding: 0;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.custom-select__item[aria-selected=true] {
  color: #454eec;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-select__item[aria-selected=true]:hover, .custom-select__item[aria-selected=true]:focus {
    color: #64696c;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .custom-select__item:hover, .custom-select__item:focus {
    opacity: 0.6;
  }
}
.custom-select__text {
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 18px;
  height: 18px;
  margin-left: 5px;
}
.custom-select__icon svg {
  transition: transform 0.3s ease;
}
.custom-select.is-disabled {
  pointer-events: none;
}
.custom-select.is-disabled .custom-select__list {
  display: none;
}
.custom-select.open-up .custom-select__list {
  bottom: auto;
  top: 0;
  transform: translateY(calc(-100% + 30px));
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon svg {
  transform: rotate(180deg);
  transition: transform 0.3s ease;
}
.custom-select.is-open .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.not-empty .custom-select__icon svg {
  color: #454eec;
}

.custom-select--filter-style {
  position: relative;
}
.custom-select--filter-style::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -18px;
  margin-top: -11px;
  width: 16px;
  height: 16px;
  background-image: url("../img/icon-filter.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.header {
  background-color: #ffffff;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
}
.header__wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px 26px 32px;
}
.header__controls {
  flex-shrink: 0;
  margin: 0 0 35px 0;
  width: 100%;
}
.header__logo {
  flex-shrink: 0;
  margin: 0 0 38px 0;
}
.header__logo svg {
  color: #52b3f2;
}
.header__logo-link {
  display: block;
  width: max-content;
}
.header__navigation {
  flex-grow: 1;
  margin: 0 0 40px 0;
}
.header__accessory {
  flex-shrink: 0;
  padding-top: 30px;
  border-top: 1px solid #f3f3f8;
}

.controls {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.controls__item:not(:first-child) {
  margin-left: 10px;
}
.controls__link {
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.controls__link--close {
  background-color: #fd4343;
}
.controls__link--wrap {
  background-color: #fdbe43;
}
.controls__link--full {
  background-color: #43fd6c;
}

.navigation {
  width: 100%;
}
.navigation__wrapper {
  display: flex;
  flex-direction: column;
}
.navigation__title {
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 12px;
  border-radius: 10px;
  transition: background-color 0.3s ease;
}
.navigation__title p {
  display: block;
  margin: 0;
  padding: 0;
  margin-left: 15px;
}
.navigation__title svg:last-of-type {
  margin-left: auto;
  transition: transform 0.3s ease;
}
.navigation__title--select {
  background-color: #f3f3f8;
}
.navigation__title--select svg:last-of-type {
  transform: rotate(180deg);
  transition: transform 0.3s ease;
}
.navigation__title:hover {
  background-color: #f3f3f8;
  transition: background-color 0.3s ease;
}
.navigation__content {
  height: 0;
  overflow: hidden;
  padding: 0 12px;
  background-color: #ffffff;
  transition: height 0.3s ease;
}
.navigation__content p {
  margin: 0;
  padding: 0;
}

.accessory {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.accessory__item:not(:last-child) {
  margin: 0 0 30px 0;
}
.accessory__link {
  display: flex;
  align-items: center;
}
.accessory__link svg {
  margin-right: 15px;
  fill: none;
}

.main-header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 20px;
  margin-bottom: 30px;
}
.main-header__search {
  grid-column: 1/3;
  position: relative;
}
.main-header__search input {
  width: 100%;
  border: none;
  height: 54px;
  padding: 16px 0 16px 56px;
  border-radius: 10px;
  background-color: #ffffff;
}
.main-header__search input::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  font-family: "Neue", "Arial", sans-serif;
  color: #64696c;
}
.main-header__search input:focus {
  outline: none;
}
.main-header__search svg {
  color: #64696c;
  position: absolute;
  left: 14px;
  top: 50%;
  margin-top: -13px;
}
.main-header__personal-add-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  background-color: #454eec;
  color: #ffffff;
}
.main-header__personal {
  display: flex;
  align-items: center;
}

.tools {
  margin: 0 0 0 auto;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.tools__item:not(:first-child) {
  margin-left: 22px;
}
.tools__link {
  color: #64696c;
}
.tools__link--unread {
  position: relative;
}
.tools__link--unread::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #98f8a0;
  top: -10px;
  right: -1px;
}

.dashboard__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: min-content 1fr 1fr 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}
.dashboard__title {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 50px;
  line-height: 60px;
  font-family: "Neue", "Arial", sans-serif;
  grid-column: 1/-1;
  grid-row: 1/2;
}
.dashboard__block--overview {
  grid-column: 1/3;
  grid-row: 2/4;
}
.dashboard__block--income {
  grid-column: 1/3;
  grid-row: 4/-1;
}
.dashboard__block--popular {
  grid-column: 3/-1;
  grid-row: 2/5;
}
.dashboard__block--comments {
  grid-column: 3/-1;
  grid-row: 5/-1;
}